import { Component, OnInit } from '@angular/core';
import { ModalForm } from 'src/app/models/modal-form';
import { ModalFormInterface } from 'src/app/interfaces/modal-form-interface';
import { PlatformLocation } from '@angular/common';
import { ModalController } from '@ionic/angular';
import { TransferDataService } from 'src/app/services/transfer-data.service';

@Component({
  selector: 'app-modal-filter',
  templateUrl: './modal-filter.page.html',
  styleUrls: ['./modal-filter.page.scss'],
})
export class ModalFilterPage extends ModalForm implements OnInit,ModalFormInterface {


  public teams = [];
  public oldTeams = [];
  public textSearch = '';

  constructor(
    public viewCtrl: ModalController,
    public location: PlatformLocation,
    public dataTranfer : TransferDataService

  ) { 
    super(location , viewCtrl);

    this.teams = dataTranfer.getData('teams');
    this.oldTeams = [... this.teams];
  }

  initObject() {
    
  }

  dismiss(team) {
    this.viewCtrl.dismiss(
      {
        team : team,
      }
    );
  }

  close(){
    this.viewCtrl.dismiss();
  }


  filterTeams(text){

    //let text = event.target.value;
    if(text.length <= 0){
      this.teams = this.oldTeams;
      return;
    }
    this.teams = [... this.oldTeams];

    this.teams = this.teams.filter(team => team.name.toUpperCase().includes(text.toUpperCase()) || team.id == -1);

  }

  getFilter(){
    return this.teams.filter(team => team.name.toUpperCase().includes(this.textSearch.toUpperCase()) || team.id == -1 )
  }

  cancelFilter($event){
    this.teams = this.oldTeams;
  }

  filterChangue(event){
    this.textSearch = event.detail.value;

    //this.filterTeams(text);
  }
  log(event){
    console.log(event)
  }

}
