import { Component, OnInit } from '@angular/core';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { ModalController, NavParams } from '@ionic/angular';
import { PhotoService } from 'src/app/services/photo.service';
import { Model } from 'src/app/api/models/model';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { UtilService } from 'src/app/services/util.service';
import { ChatService } from 'src/app/services/chat.service';
import { TransferDataService } from 'src/app/services/transfer-data.service';
import { ModalFilterPage } from '../../team/modal-filter/modal-filter.page';
import * as moment from 'moment';
import { ListFieldsPage } from '../../field/list-fields/list-fields.page';

@Component({
  selector: 'app-create-game-admin',
  templateUrl: './create-game-admin.page.html',
  styleUrls: ['./create-game-admin.page.scss'],
})
export class CreateGameAdminPage implements OnInit {

  public myTeams = []
  public teams = [];

  public teamLocal ;

  public teamsSelect = {
    v:'',
    l:''
  }

  public game : any;
  public numbGoals = []
  public id : any;
  public customDayShortNames = ['s\u00f8n'];
  public status = ['Pendiente','Suspendido','Jugado','Created'];
  public toDate : any;

  public serviceGame : Model;
  public serviceTeam : Model;

  customActionSheetOptions: any = {
    header: 'Equipo Local',
  };

  constructor(
    public request : RequestService,
    public dialogService : DialogService,
    public viewCtrl: ModalController,
    public photoService : PhotoService,
    navParams: NavParams,
    public authUser : AuthUserService,
    public util : UtilService,
    public chatService : ChatService,
    public transfData : TransferDataService,

  ) {
    this.serviceGame = new Model('Game',request);
    this.serviceTeam =  new Model('Team',request);
    //this.serviceTeam.setModel('Team');

    this.id = navParams.get('id') ;
    this.toDate = navParams.get('date') ??  util.setFormatMoment(new Date() , 'YYYY-MM-DD');
    console.log('date',this.toDate)

    this.test();
    
   }

  test(){
    console.log('day default :',moment().isoWeekday());

    console.log('day 4 :',moment().isoWeekday(6));
  }

  ngOnInit() {


    this.myTeams = this.authUser.user.teams;

    this.teamsSelect.v = this.myTeams[0];


    this.serviceTeam.api_function('getAllTeamsSelect').subscribe(data => {
      if(data['data']){
        this.teams = data['data']
        console.log('teams -> ' , this.teams)
        this.teams.push({
          'id' : -1,
          'name' : 'Equipo nuevo'
        })
        console.log('add Otro')
        //this.serviceGame.setModel('Game');
      }
    });


    for (var i = 0; i <= 30; i++) {
      this.numbGoals.push(i);
    }
    this.initObject();
  }

  initObject(){
    this.game = { 
      l_team :'',
      v_team : '',
      l_goals : 0,
      v_goals : 0,
      time : '15:00',
      date : this.toDate,
      location : '',
      description : '',
      status : 'Pendiente',
      field : null,
      field_id : ''
    };

    if(this.id){
      this.serviceGame.api_find(this.id).subscribe(data => {
        if(data['status'] == 'success'){
          this.game = data['Game'];
        }
      })
    }

    //this.chatService.show();
  }


  async submit(){

    this.game.date =  this.util.setFormatMoment(this.game.date , 'YYYY-MM-DD');

    try{
      this.valid();
    }catch(e){
      this.dialogService.showToast(e.message,'Error','danger');
      return;
    }

    await this.dialogService.loadingProgres();

    this.game.team_creator = this.teamsSelect['v']['id'];

    this.serviceGame.api_function('createGameAdmin',this.game).subscribe(
      response => {
        if(response['status'] == 'success'){
          //this.dialogService.presentAlertAcept('Felicidades...','El partido se ha registrado correctamente. Será visible en la lista de partidos cuando el administrador general o uno de los administradores del equipo contrario confirme el partido. Recibirás una notificación cuando esto suceda',()=>{
            this.dialogService.showToast('Partido registrado');

            this.dismiss(response['data']);
          //})

        }else if( response['status'] == 'error'){
          this.dialogService.showToast(response['errorValidaciones'] ? response['errorValidaciones']  : response['mensaje']  ,'','danger',5000);
        }
        this.dialogService.dimissLoaging();
      },
      error => {
        console.log(error)
        this.dialogService.dimissLoaging();
        this.dialogService.showToast(error.error.mensaje,'','danger',7000);
      },
      ()=>{
        this.dialogService.dimissLoaging();
      }
    )
  }

  dismiss(game) {
    this.chatService.hide();
    this.viewCtrl.dismiss(
      {
        game : game,
      }
    );
  }

  close(){
    this.chatService.hide();
    this.viewCtrl.dismiss();
  }

  myDateFilter = (d: Date): boolean => {
    const day = d.getDay();
    // Prevent Saturday and Sunday from being selected.
    return day == 6 ;
  }

  print(){
    console.log(this.game);
  }

  selectLocal(event){
    let e = event.detail.value;
    this.game.l_team = this.teamsSelect[e].id;
    let i = e == 'v' ? 'l' : 'v';
    this.game.v_team = this.teamsSelect[ i ].id;

    if(this.teamsSelect[ e ] ){
      this.setField(this.teamsSelect[ e ]['field']);
    }

    console.log(this.game);
  }

  clearLocal(){
    this.teamLocal = null;
    this.game.l_team = '';
    this.game.v_team = '';
  }

  valid(){
    console.log('valid Game')
    if(! this.teamsSelect.l){
      throw new Error('No ha seleccionado el equipo contrario');
    }

    if(this.teamsSelect.v['id'] == this.teamsSelect.l['id']){
      throw new Error('Los equipos enfrentados deben ser diferentes')
    }

    if(this.teamsSelect.l['id'] == -1 && !this.game.other_team){
      throw new Error('Debes añadir el nombre del Equipo Nuevo.')
    }

    if(! this.teamLocal){
      throw new Error('Debe seleccionar el equipo local');
    }

    if(! this.game.date){
      throw new Error('Debe seleccionar la fecha.');
    }

    if(moment(this.game.date).isoWeekday() != 6){
      throw new Error('La fecha seleccionada debe ser de un SÁBADO.');
    }

  }

  async selectTeam(teams,typeTeam){

    this.transfData.setData('teams',teams);

    const modal = await this.viewCtrl.create({
      component : ModalFilterPage
    })

    modal.onDidDismiss().then(data=>{
      const team = data.data['team'];
      if(team){
        this.teamsSelect[typeTeam] = team;
        this.clearLocal();
      }
    })

    await  modal.present();
  }

  async selectField(){

    this.transfData.setData('isSelect', true);

    const modal = await this.viewCtrl.create({
      component : ListFieldsPage
    })

    modal.onDidDismiss().then(data=>{
      console.log('close modal select field')
      if(data.data && data.data['field']){
        let field = data.data['field'];
        this.setField(field);
        //this.game.field = data.data['field'];
        //this.game.field_id = this.game.field.id;
      }
    })

    await  modal.present();
  }

  setField(field){
    if(field){
      this.game.field = field;
      this.game.field_id = field.id;
    }else{
      this.game.field = null;
      this.game.field_id = null;
    }
  }

}
